<template>
	<div class="basic">
		<div class="backup">
			<div class="content">
				<h1>{{text}}</h1>
				<div class="button-gtp">
					<button v-if="!onlyYes" @click="back(0)">否</button>
					<button @click="back(1)">确定</button>
				</div>
				
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		props:{
			text:{
				type:String,
				require:true,
				default:'nothing'
			},
			onlyYes:{
				type:Boolean,
				default:false
			},
			type:{
				type:String
			}
		},
		methods:{
			back(index){
				this.$emit('close',index,this.type);
			}
		}
	}
</script>
<style type="text/css" lang="less" scoped>
	@mainColor:rgb(52,152,219);
	.basic{
		position: fixed;
		top:0;
		left:0;
		background-color: rgba(0,0,0,0.4);
		display: flex;
		justify-content:center;
		align-items:center;
		.backup{
			width: 600px;
			height:300px;
			background-color:white;
		}
		.content{
			width:100%;
			height:100%;
			display: flex;
			flex-direction:column;
			justify-content:center;
			align-items:center;
			h1{
				margin-bottom:50px;
			}
			button{
				width:100px;
				height:40px;
				background-color:white;
				color:black;
				border-color: black;
				border-radius: 5px;
				box-shadow: 0 0 0 0;
				cursor:pointer;
				transition:all .4s;
				font-size: 18px;
				&:hover{
					color:white;
					background-color:@mainColor;
					border-color:@mainColor;
					box-shadow: 0 0 0 0;
				}
			}
			.button-gtp button:nth-child(2){
				margin-left:30px;
			}
		}
	}
</style>


