<template>
	<div class="basic">
		<div class="content">
			<form>
				<div>
					<label>账号</label>
					<input type="text" v-model="inputMessage[0].value" placeholder="请输入账号">
				</div>
				<div>
					<label>密码</label>
					<input type="password" v-model="inputMessage[1].value" placeholder="请输入密码">
				</div>
				<button type="button" @click="check">登录</button>
			</form>
		</div>
		<transition name="showModal">
			<modal :text="modalText" :onlyYes = 'onlyYes' v-show="isShowModal" @close="closeModal" ></modal>
		</transition>
	</div>
</template>
<script type="text/javascript">
import modal from './function/modal'
import md5 from '../../static/md5.min.js'
	export default{
		components:{
			modal
		},
		data(){
			return{
				inputMessage:[{
					name:'账号',
					kind:'account',
					value:null
				},{
					name:'密码',
					kind:'password',
					value:null
				}],
				modalText:'',
				onlyYes:false,
				isShowModal:false
			}
		},
		methods:{
			closeModal(i){
				this.isShowModal = false;
			},
			check(){
				for(var i = 0;i < this.inputMessage.length;i++)
				{
					if(!this.inputMessage[i].value || String(this.inputMessage[i].value).replace(/' '/g, '') == '')
					{
						this.modalText = this.inputMessage[i].name+'的输入为空，请重新输入！';
						this.onlyYes = true;
						this.isShowModal = true;
						return;
					}
				}

				var personData = JSON.parse(localStorage.getItem('personalMessage'));
				for(i = 0;i<this.inputMessage.length;i++)
				{
					if(this.inputMessage[i].kind == 'password')
					{
						if(md5(this.inputMessage[i].value) != personData.password)
						{
							this.modalText = this.inputMessage[i].name+'有误，请重新输入！';
							this.onlyYes = true;
							this.isShowModal = true;
							return;
						}
					}
					else if(this.inputMessage[i].value != personData[this.inputMessage[i].kind])
					{
						this.modalText = this.inputMessage[i].name+'有误，请重新输入！';
						this.onlyYes = true;
						this.isShowModal = true;
						return;
					}
				}
				//设置本地数据Time
				var curTime = (new Date()).getTime();
				var timeData = {
					lastTime:curTime,
					nextTime:curTime + Number(personData["timeInterval"])*60*1000+Number(personData["relaxTime"])*60*1000,
					timeInterval: Number(personData["timeInterval"])*60*1000+Number(personData["relaxTime"])*60*1000
				}
				localStorage.setItem('TimeMessage', JSON.stringify(timeData));

				//跳转
				localStorage.setItem('isLogin', 'true');
				this.$router.push("index");

			}
		},
		beforeCreate(){
			//跳转
			if(localStorage.getItem('isLogin') == 'true')
			{
				this.$router.push('index');
			}
			if(!localStorage.getItem('personalMessage'))
			{
				this.$router.push('logon');
			}
			
		},
		mounted(){
			
		}
	}
</script>
<style type="text/css" lang="less" scoped>
	@mainColor:rgb(52,152,219);
	.basic{
		background-color: @mainColor;
		width: 100%;
		height:100%;
		display: flex;
		justify-content:center;
		align-items:center;
	}
	.content{
		width:500px;
		height:300px;
		background-color:white;
		display:flex;
		flex-direction:column;
		align-items:center;
		justify-content:center;
		label{
			display:inline-block;
			font-size:20px;
			width:30%;
		}
		input{
			width:60%;
			height:20px;
			margin-bottom:30px;
			font-size:18px;
			padding: 10px;
		}
		button{
			font-size: 20px;
			width:350px;
			height:40px;
			margin-top:20px;
			background-color:white;
			border-color: @mainColor;
			border-style: solid;
			cursor: pointer;
			transition:all .4s;
			color:@mainColor;
			border-image: none;
			&:hover{
				border-style:solid;
				background-color:@mainColor;
				color:white;
				border-color:white;
			}
			&:active{
				background-color:darken(@mainColor, 20%)
			}
		}
	}
</style>


