<template>
	<div class="basic backup">
		<div class="content">
			<h1>注册</h1>
			<form>
				<div class="form-div" v-for="item in logonData">
					<label for="">{{item.name}}</label>
					<input type="text" v-model="item.value" @focus="focusInput" @focusout= "focusOut" :placeholder="item.text" :type="item.type?item.type:'text'">
				</div>
				<button type="button" @click="check">注册</button>
			</form>
		</div>
		<transition name="showModal">
			<modal :text="modalText" :onlyYes = 'onlyYes' v-show="isShowModal" @close="closeModal" ></modal>
		</transition>
	</div>
</template>
<script type="text/javascript">
import md5 from '../../static/md5.min.js'
import modal from './function/modal'
	export default {
		components:{
			modal
		},
		data(){
			return{
				logonData:[{
					name:'账号',
					kind:'account',
					value:null,
					text:'请输入账号'
				},{
					name:'密码',
					value:null,
					type:'password',
					kind:'password',
					text:'请输入密码'
				},{
					name:'称呼',
					value:null,
					kind:'name',
					text:'请输入昵称'
				},{
					name:'个人铭语',
					value:null,
					kind:'word',
					text:'请输入个人铭语'
				},{
					name:'休息时间',
					kind:'relaxTime',
					value:null,
					text:'请输入休息所用时间(单位：分钟)'
				},{
					name:'休息间隔',
					kind:'timeInterval',
					value:null,
					text:'请输入休息的间隔(单位：分钟)'
				}],
				modalText:' ',
				isShowModal:false,
				onlyYes:false
			}
		},
		methods:{
			focusInput(event){
				event.target.className =""
			},
			focusOut(event){
				event.target.className = 'animated rubberBand'
			},
			check(){
				for(var i = 0;i < this.logonData.length;i++)
				{
					if(!this.logonData[i].value || String(this.logonData[i].value).replace(/' '/g, '') == '')
					{
						this.modalText = this.logonData[i].name+'的输入为空，请重新输入！';
						this.onlyYes = true;
						this.isShowModal = true;
						return;
					}
				}
				var saveData ={};
				for(i = 0;i < this.logonData.length;i++)
				{
					saveData[this.logonData[i].kind]=this.logonData[i].value;
				}
				saveData.password = md5(saveData.password);
				saveData = JSON.stringify(saveData);
				localStorage.setItem('personalMessage',saveData);
				//跳转

				this.$router.push('login');
			},
			closeModal(i){
				this.isShowModal = false;
			}
		},
		beforeCreate()
		{
			//跳转检测
			if(localStorage.getItem('isLogin') == 'true')
			{
				this.$router.push('index');
			}
			if(localStorage.getItem("personalMessage"))
			{
				this.$router.push("login");
			}
			
		}

	}
</script>
<style   lang="less" scoped>

@mainColor:rgb(52,152,219);
	.backup{
		background-color: @mainColor;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.content{
		width: 600px;
		background-color: white;
		height: 580px;
		border-radius: 5px;
		display: flex;
		flex-direction: column;
		align-items: center;
		h1{
			text-align:center;
			margin-bottom: 50px;
			margin-top: 50px;
		}
		form{
			width:80%;
		}
		button{
			width:80%;
			height:50px;
			border:2px solid @mainColor;
			background-color: white;
			display:block;
			margin:0 auto;
			margin-top:20px;
			cursor:pointer;
			transition:all .4s;
			border-radius: 5px;
			font-size:20px;
			&:hover{
				background-color:@mainColor;
				transform:scale(1.1,1.1);
			}
			&:active{
				border:2px solid transparent;
			}
		}
	}
	.form-div{
		height:50px;
		label{
			text-align: left;
			display:inline-block;
			width:30%;
			font-size: 20px;
		}
		input{
			height:35px;
			width:60%;
			font-size:16px;
		}
	}
</style>


