<template>
	<div class="basic">
		<top></top>
		<div class="middle" :style="{'background-image':'url('+bg+')'}">
			<div class="content">
				<div class="block" style="margin-right: 6.25%;"></div>
				<div class="block" style="margin-right: 6.25%;"></div>
				<div class="block"></div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import top from "@/components/top"
	export default{
		components:{
			top
		},
		beforeCreate()
		{
			//跳转检测
			if(localStorage.getItem('isLogin') == 'true')
			{
				
			}
			else if(localStorage.getItem("personalMessage"))
			{
				this.$router.push("login");
			}else
				this.$router.push('logon');
			
		},
		data(){
			return{
				bg:require('../../static/index1.jpg')
			}
		}
	}
</script>
<style type="text/css" lang="less" scoped>
	.middle{
		width: 100%;
		height: 95%;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
	}
	.content{
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.1);
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.block{
		height: 80%;
		width: 25%;
		background-color: rgba(255, 255, 255, 0.9);
		border-radius: 0px;
		display:flex;
		flex-direction:column;

		.title{
			width:100%;
			text-align:center;
			height:100px;
			line-height: 100px;
		}
		.mission{
			height:6%;
			margin:0 20px 20px 20px;			
			transition:all .4s;
			cursor:pointer;
			border-radius:5px;
		}
		.red{
			border: 2px solid rgba(244, 67, 54,0.5);
			&:hover{
				background-color:rgba(244,67,54,0.8);
				transform:scale(1.2,1.2);
			}
		}
		.blue{
			border: 2px solid rgba(52,152,219,1);
			&:hover{
				background-color:rgba(52,152,219,0.8);
				transform:scale(1.2,1.2);
			}
		}
		img{
			width:48px;
			align-self:center;
		}
	}
</style>


