import Vue from 'vue'
import Router from 'vue-router'
import test from '@/components/test'
import logon from '@/components/logon'
import login from '@/components/login'
import index from '@/components/index'

Vue.use(Router)

export default new Router({
  routes: [{
    	path:'/test',
    	name:'test',
    	component:test
    },{
    	path:'/logon',
      name:'logon',
    	component:logon
    },{
      path:'/login',
      name:'/login',
      component:login
    },{
      path:'/index',
      name:'/index',
      component:index
    },{
      path:'*',
      redirect:'/logon'
    }
  ]
})



