<template>
	<div class="top">
		<h2>你好，{{personData.name}}</h2>
		<h3>{{personData.word}}</h3>
		<p>时间：{{time}}<button @click="relax">提前休息</button></p>
		<button>修改个人信息</button>
		<button @click="logout">注销</button>
		<modal v-show="isShowModal" :text="modalText" :type="modalType" :onlyYes="modalChoice" @close="close"></modal>
	</div>
</template>
<script type="text/javascript">
import modal from './function/modal'
	export default{
		components:{
			modal
		},
		beforeCreate(){

		},
		mounted(){
			this.personData = JSON.parse(localStorage.getItem("personalMessage"));
			this.timeData = JSON.parse(localStorage.getItem("TimeMessage"));
			this.firstTime();
			this.TimeInv = setInterval(this.setTime, 1000);
		},
		data(){
			return{
				personData:{},
				time:'',
				TimeInv:null,
				timeData:{},
				isShowModal:false,
				modalText:'',
				modalType:'',
				modalChoice:false
			}
		},
		beforeDestroy(){
			clearInterval(this.TimeInv);
		},
		methods:{
			setTime(){
				var curTime = new Date();
				this.time = curTime.toTimeString().split(' ')[0];

				//check
				if(curTime.getTime() >= this.timeData.nextTime)
				{
					//updata localMessge-TimeMessage 
					var newTimeData = {
						lastTime:curTime.getTime(),
						nextTime:curTime.getTime() + this.timeData.timeInterval,
						timeInterval:this.timeData.timeInterval
					}

					localStorage.setItem("TimeMessage", JSON.stringify(newTimeData) );
					this.timeData = newTimeData;
					//Notification start
					if(Notification.permission == 'granted')
					{
						var n1 = new Notification("提示",{
							body:"休息时间到了~请活动一下！",
							icon:"",
							silent:true,
							sound:""
						});
						
						n1.onclick = ()=>{
							n1.close();
						}
					}else{
						Notification.requestPermission();
						alert("休息时间到了~请活动一下！请允许打开提醒，有更好的体验哦！");
					}

				}

			},
			relax(){
				var curTime = new Date();
				var newTimeData = {
					lastTime:this.timeData.lastTime,
					nextTime:curTime.getTime(),
					timeInterval:this.timeData.timeInterval
				}

				localStorage.setItem("TimeMessage", JSON.stringify(newTimeData) );
				this.timeData = newTimeData;
			},
			logout(){
				this.isShowModal  = true;
				this.modalText = "确认注销吗？";
				this.modalType='logout';
				this.modalChoice=false;
			},
			firstTime(){
				clearInterval(this.TimeInv);
				var curTime = new Date();
				this.time = curTime.toTimeString().split(' ')[0];

				if(curTime.getTime() >= this.timeData.nextTime)
				{
					var newTimeData = {
						lastTime:curTime.getTime(),
						nextTime:curTime.getTime() + this.timeData.timeInterval,
						timeInterval:this.timeData.timeInterval
					}

					localStorage.setItem("TimeMessage", JSON.stringify(newTimeData) );
					this.timeData = newTimeData;
				}
			},
			close(index,type){
				switch (type) {
					case 'logout':
						this.isShowModal = false;
						if(index)
						{
							localStorage.setItem("isLogin", "false");
							this.$router.push('login');
						}
						break;
					default:
						this.isShowModal = false;
						break;
				}
			}
		}


	}
</script>
<style type="text/css" lang="less" scoped>
@mainColor:rgb(52,152,219);
	.top{
		height:65px;
		width: 100%;
		display: flex;
		padding:10 10%;
		justify-content:space-around;
		align-items:center;
		button{
			border-style: none;
			height:30px;
			width:100px;
			background-color: @mainColor;
			cursor:pointer;
			color:white;
			transition:all .4s;
			&:hover{
				transform:scale(1.5,1.5);
				border-style:none;
			}
			&:active{
				background-color:darken(@mainColor,10%);
			}
		}
		p{
			button{
				margin-left: 50px;
			}
		}
	}
</style>


